<template>
  <div class="position-relative">
    <div>
      <div class="bg-white border-bottom d-md-block d-none">
        <div class="d-flex align-items-center container justify-content-between py-3">
          <div class="d-flex align-items-center">
            <h2 class="first-letter-caps mb-1">{{ project && project.name ? project.name : "Project Name" }}</h2>
            <p class="mb-0 text-muted ml-2">{{ project && project.description ? project.description : "Project Description" }}</p>
          </div>
        </div>
      </div>
      <Mocks ref="mockDetails" :project="project" />
    </div>
  </div>
</template>
<script>
  import Mocks from "./Mocks";
  import Mockly from "@/js/mockly/mockly";

  export default {
    components: {
      Mocks,
    },
    data() {
      return {
        domainId: window.domain,
        projectId: window.user,
        project: null,
      };
    },

    async mounted() {
      await this.getProject();
    },
    methods: {
      async getProject() {
        this.project = await Mockly.getProject(this.domainId, this.projectId, this.$route.query.p);
      },
    },
  };
</script>

